.keypad {
    margin: 0 auto;
}

.content {
    border-radius: 20px;
    font-family: sans-serif;
    height: auto;
    padding: 0.6rem;
    width: 516px;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    box-shadow: -10px -10px 30px 4px rgba(154, 203, 255, 0.15), 10px 10px 30px 4px rgba(154, 203, 255, 0.15);
}

@media (max-width: 992px) {
    .content {
        width: 344px;
    }
}

@media (max-width: 572px) {
    .content {
        width: 258px;
        min-width: 258px;
    }
}

.content div {
    width: 70px;
    height: 70px;
    border-radius: 14px;
    cursor: pointer;
    margin: 0.5rem;
    font-size: 1.4rem;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 250ms ease;
    background: #892517
}

.content div:hover {
    background: #892517
}

.content div:active {
    background: #f44336;
}

.content .b6 {
    order: 1;
}

@media (max-width: 992px) {
    .content .b6 {
        order: 0;
    }
}

.content .b7 {
    order: 1;
}

@media (max-width: 992px) {
    .content .b7 {
        order: 0;
    }
}

@media (max-width: 572px) {
    .content .b7 {
        order: 0;
    }
}

.content .b8 {
    order: 1;
}

@media (max-width: 572px) {
    .content .b8 {
        order: 0;
    }
}

.content .b9 {
    order: 1;
}

.content .b0 {
    order: 1;
}

.content .enter {
    background: #8bc34a;
    color: #FFFFFF;
    font-size: 1rem;
    order: 1;
}

.content .enter:hover {
    background: #4CAF50;
}

.content .enter:active {
    background: #9acd5e;
}

.content .clear {
    background: #fff3cb;
    color: #474747;
    font-size: 1rem;
    order: 3;
}

.content .clear:hover {
    background: #ffecae;
}

.content .clear:active {
    background: #fee38b;
}

.keypad .dots {
    justify-content: center;
    display: flex;
    margin: 1.5rem 0;
}

.keypad .dots div {
    width: 1rem;
    height: 1rem;
    background: transparent;
    margin: 0.7rem 1rem;
    border-radius: 50%;
    border: 1px solid #F44336;
}

.keypad .dots .active-value {
    background: #cb4037;
}