.final-container {
    color: rgba(255, 255, 255, 1);
    width: 100%;
    font-family: 'Mountains of Christmas', cursive;
}

.final-container h1 {
    font-size: 42px;
    text-align: center;
}

.final-container h4 {
    font-size: 42px;
    text-align: center;
}

.final-container>.ns-logo {
    fill: #fff;
}

.final-moon {
    align-items: flex-end;
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/1231630/moon2.png") no-repeat;
    background-size: 400px;
    background-position: right;
    background-position-y: 10px;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    opacity: 0.3;
    box-shadow: inset 0px 0px 20px 20px #000000;
    text-align: center;
    font-size: 40px;
    font-family: 'Mountains of Christmas', cursive;
}