.countdown-container h1 {
    font-weight: normal;
}

.countdown-container li {
    display: inline-block;
    font-size: 1.5em;
    list-style-type: none;
    padding: 1em;
    text-transform: uppercase;
}

.countdown-container li span {
    display: block;
    font-size: 4.5rem;
}

.intro-container {
    margin: 0 auto;
    text-align: center;
    font-family: 'Mountains of Christmas', cursive;
    color: #fff;
}

.winner-container div {
    margin: 0 10px;
}

.winner-container div:nth-child(1) {
    font-size: 36px;
}

.winner-container div:nth-child(2) {
    font-size: 24px;
}

.winner-container div:nth-child(3) {
    font-size: 18px;
}