.btn-root {
    width: 300px;
    height: 70px;
    border-radius: 14px;
    cursor: pointer;
    margin: 0.5rem auto;
    font-size: 1.4rem;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 250ms ease;
    background: rgba(137, 38, 23, 0.8);
    outline: none;
    border-color: rgba(0, 0, 0, 0.4);
    box-shadow: 0 0 9px 2px rgba(137, 38, 23, 0.8);
}

.btn-root:hover {
    background: #892517
}

.btn-root:active {
    background: #f44336;
}