.light-project-container {
    height: 20px;
    width: 30%;
    background: rgba(0, 0, 0, 0.4);
    margin: 10px auto;
    border-radius: 25px;
    transition: all 250ms ease;
    color: rgba(0, 0, 0, 0);
    text-align: center;
    padding: 10px 0;
}

.light-project-container.on1 {
    background: rgba(205, 210, 214, 0.42);
    box-shadow: 0px 0px 20px 20p rgba(248, 232, 67, 0.3);
}

.light-project-container::selection {
    background-color: #222;
    color: white;
}